
import { _decorator, Component, Node, Collider2D, IPhysics2DContact, Contact2DType } from 'cc';
import { PoolObjects } from './PoolObjects';
const { ccclass, property } = _decorator;

 
@ccclass('PlayerEnemy')
export class PlayerEnemy extends PoolObjects {

    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) 
    {
        if (otherCollider.tag == 5 || otherCollider.node.name == "Bullet" || otherCollider.node.name == "Bomb")
        {
            //kill enemy
            this._isHit = true;
        }
    }

    update (deltaTime: number) {

        this.Moving(deltaTime);

        if (this._isHit)
            //if enemy node is out of bound/hit by bullet/hit by bomb - return to pool and set the position back to 0,0,0
            this.BackToPool();
    }


}

